body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {

}

:root {
     --precipitation: lightblue;
}




#root {
     width: 100vw;
     height: 100vh;
     display: -webkit-flex;
     display: flex;
     position: fixed;
     -webkit-flex-direction: column;
             flex-direction: column;
     -webkit-justify-content: center;
             justify-content: center;
     -webkit-align-items: center;
             align-items: center;
     background: -webkit-linear-gradient(bottom, rgb(255, 255, 255) 50%, #fff 50%);
     background: linear-gradient(0deg, rgb(255, 255, 255) 50%, #fff 50%); /* rgb(249, 245, 148) */
     /* #ebebeb is a nice grey */
}


.App {
     position: absolute;
     width: 1000px;
     height: 650px;
     box-shadow: 0px 0px 20px 2px rgba(100,100,100,.2);
     background-color: white;
     z-index: 1;
     overflow: hidden;
}

#search {
     border: none;
     border-bottom: 5px solid black;
     width: 350px;
     height: 50px;
     padding: 3px 0px;
     margin: 30px  55px;
     background-color: transparent;
     font-family: 'Work Sans';
     font-size: 30px;
     font-weight: 900;
     font-style: normal;
     color: black;
     z-index: 1;
}

::-webkit-input-placeholder {
     opacity: .4;
}

::-ms-input-placeholder {
     opacity: .4;
}

::placeholder {
     opacity: .4;
}

#search:focus {
     outline: none;
}

#submit {
     background-color: black;
     border: 1px solid black;
     height: 26px;
     width: 75px;
     position: relative;
     right: 45px;
     padding-left: 30px;
     border-radius: 13px;
     color: white;
     text-align: center;
}

#submit:focus {
     outline: none;
}

.weather-info {
     max-width: 570px;
}

.search-stuff {
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: row;
             flex-direction: row;
     -webkit-align-items: center;
             align-items: center;
     width: 500px;
}

h5 {
     margin: 0;
     margin-top: -25px;
     font-family: 'Work Sans';
     font-weight: 700;
     font-size: 28px;
     background-color: black;
     color: white;
     width: auto;
     display: inline-block;
     padding: 5px 10px;
}

h6 {
     margin: 0;
     margin-top: -35px;
     font-family: 'Work Sans';
     font-weight: 300;
     font-size: 26px;
     padding-left: 2px;
}

h3 {
     margin: 0;
     margin-top: 0px;
     font-family: 'Work Sans';
     font-weight: 800;
     font-size: 175px;
}

h4 {
     margin: 0;
     margin-top: 125px;
     font-family: 'Work Sans';
     font-weight: 800;
     font-size: 40px;
     text-transform: capitalize;
}

p {
     margin: 0;
     margin-top: 10px;
     font-family: 'Work Sans';
     font-weight: 300;
     font-size: 16px;
     width: 400px;
}

h6, h5, h3, h4, p {
     margin-left: 55px;
}

.nav {
     background-color: black;
     width: 100px;
     height: 650px;
     position: absolute;
     left: 900px;
     top: 0px;
     z-index: 2;
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: column;
             flex-direction: column;
     -webkit-justify-content: space-around;
             justify-content: space-around;
}

.link {
     display: inline-block;
     margin: 0;
     margin-top: 0px;
     padding: 95px 10px;
     font-family: 'Work Sans';
     font-weight: 400;
     font-size: 24px;
     color: white;
     width: 80%;
     text-align: center;
     -webkit-align-self: center;
             align-self: center;
     vertical-align: middle;
}

a {
     text-decoration: none;
}

a:hover {
     background-color: darkgrey;
     color: black;
     font-weight: 600;
}

#first-link {
     border-bottom: 1px white solid;
}

#last-link {
     border-top: 1px white solid;
}




